@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600;900&display=swap);
:root {
  --dark-bg-color: #161d2f;
  --light-bg-color: #4b5366;
  --light-orange: #fbbe88;
  --light-green: #569e78;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.9);
  background-image: url(https://yannickdedecker.github.io/Cookr/static/media/Background.3df2eb4b.png);
  background-repeat: repeat;
  background-position: center;
  overflow-x: hidden;
  min-height: 100vh;
}

/* COMPLETE GALLERY CONTAINER */

.gallery-block {
  padding: 2rem 0;
}

/* INDIVIDUAL GALLERY ELEMENT */

.card {
  background: none;
  min-height: 14.375rem;
}

.card-body {
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
}

.gallery-block.cards-gallery h6 {
  font-size: 1rem;
  font-weight: 900;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.9);
}

.gallery-block.cards-gallery .card {
  transition: 0.4s ease;
}

.gallery-block.cards-gallery .card img {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
}

.gallery-block.cards-gallery a:hover {
  text-decoration: none;
}

.gallery-block.cards-gallery .card {
  margin-bottom: 30px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
}

/* MAIN TAG */

.tag {
  position: absolute;
  left: 5px;
  top: 5px;
  background: var(--light-green);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.75rem;
  padding: 0.25rem;
  color: white;
}

/* SEARCH RESULTS */

.found {
  display: none;
}

.gallery-block p:first-of-type {
  margin-top: 3rem;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  padding-top: 1rem;
}

/* RESPONSIVE WEBSITE */

@media (min-width: 576px) {
  .gallery-block .transform-on-hover:hover {
    transform: translateY(-10px) scale(1.02);
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.15) !important;
  }
}

@media only screen and (max-width: 600px) {
  .card {
    background: none;
    min-height: 14rem;
  }
}

@media only screen and (min-width: 600px) and (max-width: 992px) {
  .gallery-block.cards-gallery h6 {
    font-size: 20px;
  }

  .card {
    background: none;
    min-height: 18rem;
  }
}

